      <a to="/account" class="icon-item block-mobile"><img src="./image/account.svg"></a>
      <a class="icon-item block-mobile"><img src="./image/cart.svg"></a>
    </div>
  </div>

  <div id="header2">
    <router-link to="/" class="header2-item">HOME</router-link>
    <router-link to="/new" class="header2-item">NEW</router-link>
    <router-link to="/products" class="header2-item">PRODUCTS</router-link>
    <router-link to="/about" class="header2-item">ABOUT</router-link>
    <router-link to="/support" class="header2-item">SUPPORT</router-link>
  </div>

  <!-- Search -->
  <div class="overlay">
    <section>
      <!-- 검색어 입력창   -->
      <input
        type="text"
        id="search"
        placeholder="SEARCH EVERYTHING"
        autocomplete="off"
      />
      <!-- 자동완성 단어 리스트 -->
      <div class="autocomplete"></div>
    </section>
    <div class="background"></div>
  </div>

  <!-- 햄버거 버튼 -->
  <div class="hamburger-button">
    <span></span>
    <span></span>
    <span></span>
    <span></span>
  </div> 

  <!-- 오버레이 메뉴 -->
  <div class="overlay-menu">
    <nav class="menu">
      <ul>
        <li><router-link to="/" class="menu-link">HOME</router-link></li>
        <li><router-link to="/new" class="menu-link">NEW</router-link></li>
        <li><router-link to="/products" class="menu-link">PRODUCTS</router-link></li>
        <li><router-link to="/about" class="menu-link">ABOUT</router-link></li>
