import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomeView,

  },

  {
    path: '/new',
    name: 'New',
    component: () => import('../views/NewView.vue'),
    
  },

  {
    path: '/support',
    name: 'Support',
    component: () => import('../views/SupportView.vue')
  },

  //스토리 탭

  {
    path: '/story',
    name: 'Learnmore',
    component: () => import('../views/StoryView.vue')
  },
  
  {
    path: '/story_01',
    name: 'FROSET',
    component: () => import('../views/Story_01.vue')
  },

  {
    path: '/story_02',
    name: 'BURVIK',
    component: () => import('../views/Story_02.vue')
  },
  
  {
    path: '/story_03',
    name: 'BRONDEN',
    component: () => import('../views/Story_03.vue')
  },

  //제풉 탭들

  {
    path: '/products',
    name: 'Products',
    component: () => import('../views/Products_bedding.vue')
  },

  {
    path: '/office',
    name: 'Office',
    component: () => import('../views/Products_office.vue')
  },

  {
    path: '/kitchen',
    name: 'Kitchen',
    component: () => import('../views/Products_kitchen.vue')
  },

  {
    path: '/outdoor',
    name: 'Outdoor',
    component: () => import('../views/Products_outdoor.vue')
  },

  {
    path: '/living',
    name: 'Living',
    component: () => import('../views/Products_living.vue')
  },

  {
    path: '/bath',
    name: 'Bath',
    component: () => import('../views/Products_bath.vue')
  },

  {
    path: '/lighting',
    name: 'Lighting',
    component: () => import('../views/Products_lighting.vue')
  },

  {
    path: '/rugs',
    name: 'Rugs',
    component: () => import('../views/Products_rugs.vue')
  },
  
  {
    path: '/ProductExampleView',
    name: 'Purchase',
    component: () => import('../views/ProductExampleView.vue')
  },

  {
    path: '/loremipsum',
    name: 'LoremIpsum',
    component: () => import('../views/LoremIpsum.vue')
  },


  {
    path: '/about',
    name: 'About Us',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
]


const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(){
    return { top: 0 }
  },  
})

router.beforeEach((to, from, next) => {
  const defaultTitle = '';
  const pageTitle = to.name ? to.name : defaultTitle;
  document.title = pageTitle;
  next();
})

export default router


